@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300i,400,400i,600,600i,700,700i,900&display=swap);
.led {
  width: 32px;
  height: 32px;
  border: 1px solid var(--color-black);
  background: var(--color-white);

  border-radius: 50%;
}

.led--ison {
  background: var(--color-red);
}

.led--isoff {
  background: var(--color-black);
}
.binary-clock {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-items: flex-start;
  justify-content: flex-start;
  align-items: flex-end;
}

.led-strip {
  margin: calc(var(--spacer) / 4);
}

.led {
  margin: calc(var(--spacer) / 8) 0;
}
.clock__strip {
  font-size: 3rem;
  font-weight: 900;

  background: var(--color-black);
  color: var(--color-white);
  padding: var(--gutter) calc(var(--gutter) * 2);
}
.clock__strip--h1 {
  font-size: 4rem;
}
.clock__strip--h2 {
  font-size: 6rem;
  background: var(--color-green);
}
.clock__strip--m1 {
  font-size: 5rem;
}
.clock__strip--m2 {
  font-size: 6rem;
  background: var(--color-orange);
}
.clock__strip--s1 {
  font-size: 3rem;
}
.clock__strip--s2 {
  font-size: 5rem;
  background: var(--color-purple);
}
:root {
  --color-purple: #7E57C5;
  --color-purple-dark: #412D66;
  --color-purple-darkest: #201633;
  --color-green: #5AB946;
  --color-orange: #F58732;
  --color-red: #ED0034;
  --color-black: #000;
  --color-white: #fff;
  --color-gray-lightest: #fcfcfc;
  --color-gray-light: #dfdfdf;
  --color-gray: #cfcfcf;
  --spacer: 16px;
  --font-family-sourcesanspro: 'Source Sans Pro', sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  font-family: var(--font-family-sourcesanspro);
  font-size: 16px;

  font-weight: 400;
  line-height: 1.42;

  color: #000;

  color: var(--color-black);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Source Sans Pro', sans-serif;
  font-family: var(--font-family-sourcesanspro);

  font-weight: 900;
  line-height: 1.42;
}
